import React, { Component } from 'react';
import './RightSideNav.css';
import {Link} from "react-router-dom";
import ZEVA from '../img/ZEVA.png';
import ZenAnalytica_Logo from '../img/ZenAnalytica_Logo.png';
import RoboChamp from '../img/RoboChamp.png';
import ServiceNow from '../img/ServiceNow.png';
import Cherwell from '../img/Cherwell.png';
import hp from '../img/hp.png';
import Arrow from '../img/arrow.png'



class RightSideNav extends Component{
    render(){
      
        return (
            <div>
                <div className="rightSideNav">
                    <div  style={{display: 'inline-block', textAlign: 'right',paddingLeft:"10rem"}}>
                        <span style={{fontSize: '20px',fontFamily:'Helvetica', fontWeight:'bolder', marginBottom:"2px"}}>LIBRARY</span><br></br>
                        <span style={{fontSize: '13px',fontFamily:'Helvetica', fontStyle:"oblique", marginTop:"1px"}}>Access all your services here.</span>
                    </div>
                    <br/>
                    <br/>
                    <div>
                        <span className="category"> Zensar AI-Led ITSM Tools </span><br/>
                        <span className="subCategory"> Click on service to configure </span>
                        <br/>
                        <br/>
                        <div className="RightNavServiceImg" style={{display:"inline-block",marginRight:"2rem" }}>
                            <Link to="/InstallationGuidePage">  
                                <img src={ZEVA} alt="ZEVA" style={{height: '25px',width:'51px', textAlign:'center'}}/>
                            </Link>  
                        </div>  
                        <div className="RightNavServiceImg" style={{display:"inline-block" ,marginRight:"2rem" }}>
                            <Link to="/RecommConfigurationLayout">  
                                <img src={ZenAnalytica_Logo} alt="ZEVA" style={{height: '25px',width:'49px', textAlign:'center'}}/>
                            </Link>
                        </div>
                        <div className="RightNavServiceImg" style={{display:"inline-block" ,marginRight:"2rem" }}>
                            <Link to="/LibraryLayout">  
                                <img src={RoboChamp} alt="ZEVA" style={{height: '20px',width:'51px', textAlign:'center'}}/>
                            </Link>
                        </div>

                        <div className="serviceName" style={{display:"inline-block",marginRight:"1rem" }}>
                            ZEVA
                        </div>  
                        <div className="serviceName" style={{display:"inline-block" ,marginRight:"0.5rem" }}>
                           ZenAnalytica
                        </div>
                        <div className="serviceName" style={{display:"inline-block" ,marginRight:"0.5rem" }}>
                            RoboChamp
                        </div>
                    <br/>
                    <br/>
                    <br/>
                    <br/>
                    </div>
                    <div>
                        <span className="category">Industry-Led ITSM Integration</span><br/>
                        <span className="subCategory"> Click on service to configure </span>
                        <br/>
                        <br/>
                        <div className="RightNavServiceImg" style={{display:"inline-block",marginRight:"2rem" }}>
                            <Link to="/LibraryLayout">  
                                <img src={ServiceNow} alt="ServiceNow" style={{height: '20px',width:'51px', textAlign:'center'}}/>
                            </Link>  
                        </div>  
                        <div className="RightNavServiceImg" style={{display:"inline-block" ,marginRight:"2rem" }}>
                            <Link to="/LibraryLayout">  
                                <img src={Cherwell} alt="Cherwell" style={{height: '20px',width:'49px', textAlign:'center'}}/>
                            </Link>
                        </div>
                        <div className="RightNavServiceImg" style={{display:"inline-block" ,marginRight:"2rem" }}>
                            <Link to="/LibraryLayout">  
                                <img src={hp} alt="hp" style={{height: '30px',width:'51px', textAlign:'center'}}/>
                            </Link>
                        </div>

                        <div className="serviceName" style={{display:"inline-block",marginRight:"0.5rem" }}>
                            ServiceNow
                        </div>  
                        <div className="serviceName" style={{display:"inline-block" ,marginRight:"2rem" }}>
                           Cherwell
                        </div>
                        <div className="serviceName" style={{display:"inline-block" ,marginRight:"0.5rem" }}>
                            HP
                        </div>
                    
                    <br/>
                    <br/>
                    </div>
                    <div className="more">
                        <Link to="/LibraryLayout" style={{color:"black"}}>more
                            <img src={Arrow} alt="arrow" style={{height: '10px',width:'11px', textAlign:'center'}}/>
                        </Link>
                    </div>
                </div>
            </div>
          
        );
        }


}

export default RightSideNav;