import React, { Component } from 'react';
import Toolbar from '../Toolbar/Toolbar';
import VALiveInstance from './VALiveInstance';


class LiveInstancesLayout extends Component {
  render() {

    return (
      <div className="container-fluid">
        <div className="row min-vh-100 flex-column flex-md-row ">
          <Toolbar />
          <main className="col flex-grow-1">
            <VALiveInstance />
          </main>
        </div>
      </div>
    );
  }


}

export default LiveInstancesLayout;