import React from 'react';
import Toolbar from '../Toolbar/Toolbar';
import RecommConfiguration from './RecommConfiguration';


function RecommConfigurationLayout() {
    return (
      <div className="container-fluid">
        <div className="row min-vh-100 flex-column flex-md-row ">
          <Toolbar />
          <main className="col flex-grow-1">
            <RecommConfiguration />
          </main>
        </div>
      </div>
      );
    }
export default RecommConfigurationLayout;