import React from 'react';
import {Switch, Route} from 'react-router-dom';
import DashboardLayout from './Components/DashboardLayout/DashboardLayout';
import Login from './Components/Login/Login';
import ServiceLibraryLayout from './Components/ServiceLibraryLayout/ServiceLibraryLayout';
import ChatBotConfigurationLayout from './Components/ChatBotConfigurationLayout/ChatBotConfigurationLayout';
import RecommConfigurationLayout from './Components/RecommConfigurationLayout/RecommConfigurationLayout';
import InstallationGuideLayout from './Components/InstallationGuide/InstallationGuideLayout';
import OnboardingChatBotLayout from './Components/OnboardingChatBotLayout/OnboardingChatBotLayout';
import RunningInstanceLayout from './Components/RunningInstanceLayout/RunningInstanceLayout';
import ZenAnalyticaDashboardLayout from './Components/ZenAnalyticaDashboardLayout/ZenAnalyticaDashboardLayout'
import LandingPage from './Components/LandingPage/LandingPage';
import LiveInstancesLayout from './Components/LiveInstancesLayout/LiveInstancesLayout';
import Toolbar from './Components/Toolbar/Toolbar';
import CreateInstanceLayout from './Components/CreateInstanceLayout/CreateInstanceLayout'
import IntegrationOfServiceLayout from './Components/IntegrationOfServicesLayout/IntegrationOfServiceLayout'
import RPAIntegrationLayout from './Components/RPAIntegrationLayout/RPAIntegrationLayout';
import ZAIntegrationLayout from './Components/ZAIntegrationLayout/ZAIntegrationLayout'

function App() {
  return (
    <Switch>        
      <Route exact path="/Samurai1.0" component={LandingPage}/>
      <Route path="/Login" component={Login}/>
      <Route path="/DashboardLayout" component={DashboardLayout}/>   
      <Route path="/Toolbar" component={Toolbar}/>   
      <Route path="/ServiceLibraryLayout" component={ServiceLibraryLayout}/>   
      <Route path="/ChatBotConfigurationLayout" component={ChatBotConfigurationLayout}/>   
      <Route path="/RecommConfigurationLayout" component={RecommConfigurationLayout}/>   
      <Route path="/InstallationGuideLayout" component={InstallationGuideLayout}/> 
      <Route path="/OnboardingChatBotLayout" component={OnboardingChatBotLayout}/> 
      <Route path="/RunningInstanceLayout" component={RunningInstanceLayout}/> 
      <Route path="/ZenAnalyticaDashboardLayout" component={ZenAnalyticaDashboardLayout}/>
      <Route path="/LandingPage" component={LandingPage}/> 
      <Route path="/LiveInstancesLayout" component={LiveInstancesLayout}/> 
      <Route path="/CreateInstanceLayout" component={CreateInstanceLayout}/> 
      <Route path="/IntegrationOfServiceLayout" component={IntegrationOfServiceLayout}/> 
      <Route path="/RPAIntegrationLayout" component={RPAIntegrationLayout}/> 
      <Route path="/ZAIntegrationLayout" component={ZAIntegrationLayout}/> 
  </Switch>
  );
}

export default App;
