import React, { Component } from 'react';
import Logo from '../img/Logo.svg';
import { Link } from "react-router-dom";
import tick from '../img/tick.png';
import './ChatBotConfiguration.css';


class ChatBotConfiguration extends Component {


    proceedtoIntegrationButton = () => {

        window.location = "/IntegrationOfServiceLayout";


    }
    selectInstance(){
        window.location = "/CreateInstanceLayout";
    }
    selectOnBoarding(){
        window.location = "/OnboardingChatBotLayout";
    }
    render() {
        return (
            <div className="container-fluid" >
                <div className="row justify-content-between">
                    <div className="col-4 d-none d-md-inline Onboarding-logoImg" />
                    
                    <div className="col title">
                        <span className="titleName">CONFIGURATION</span><br />
                        <span className="titleDescription">Start configuration of selected services</span>
                    </div>
                </div>
                <br />

                <div className="row">
                    <div className="col-6 d-flex d-flex justify-content-between instance-containerServices" >
                        <div className="p-2">
                            <span className=" instance-services1" >ZEVA</span>
                            <span className=" instance-services1">ZA</span>
                            <span className=" instance-services1">RPA</span>
                        </div>
                        <div className="p-2">
                            <span className="instance-services1" >ZEVA</span>
                            { /*<span className="instance-services1" >ZA</span>*/}
                        </div >
                        <div className="p-2" >
                           <span className="instance-services1">ZEVA</span>
                           { /*   <span className="instance-services1">ZA</span>*/}
                        </div>
                        <div className="p-2" >
                          { /*  <span className="instance-services1">ZEVA</span>*/}
                        </div>
                    </div>
                </div>
                
                <div className="row">
                    <div className="col-6 d-flex justify-content-between instance-containerLine" >
                        <div className="p-2" id="instance-first-tick-mark" onClick={this.selectInstance} >
                            <img className="tick" src={tick} alt="tick" />
                        </div>
                        <div className="p-2" id="instance-first-tick-mark" onClick={this.selectOnBoarding}>
                            <img className="tick" src={tick} alt="tick" />
                        </div>
                        <div className="p-2" id="instance-next-tick-mark" >
                            <img className="tick" src={tick} alt="tick" />
                        </div>
                        <div className="p-2" id="instance-next-tick-mark" >
                            <img className="tick" src={tick} alt="tick" />
                        </div>
                    </div>
                </div>
                <div className="row">
                    <div className="col-7 d-flex justify-content-between instance-containerLabel" >
                        <span className="p-2" style={{ color: "black" }}>Selection</span>
                        <span className="p-2" style={{ color: "black" }}>Onboarding</span>
                        <span className="p-2" style={{ color: "#919191" }}>Configuration</span>
                        <span className="p-2" style={{ color: "#919191" }}>Integration</span>
                    </div>
                </div>
                <br />



                <div className="row col subTitle">
                    <span style={{ fontWeight: "lighter", marginRight: "0.5rem" }}>ZEVA</span>
                    <span style={{ fontWeight: "bold" }}>Configuration</span>
                </div>

                <div className="row col-12 video"
                    style={{
                        position: "relative",
                        paddingBottom: "56.25%" /* 16:9 */,
                        paddingTop: 50,
                        height: 0

                    }}>
                    <iframe
                        style={{
                            position: "absolute",
                            top: 40,
                            left: 0,
                            width: "100%",
                            height: "85%",
                            border: "#00000029"

                        }}
                        src={`http://52.172.146.59:8080/com.zensar.zenlabs.zeva/adminupload`}
                        title="configureInstance"
                        frameBorder="0" />
                </div>

                <div className="row ">
                    <div className="col-8 "></div>
                    {/*<div className="col-2 ">
                        </div>*/}

                        <button className="col-3 btn btn-default config-proceedButton" id="config-proceedButton" onClick={this.proceedtoIntegrationButton}>
                            <label className="config-proceedLabel">PROCEED ZEVA TO SAMURAI INTEGRATION </label>
                        </button>
                    
                </div>
                <br/>
                <br/>
            </div>


        )
    }

}
export default ChatBotConfiguration;