import React, { Component } from 'react';
import './Toolbar.css';
import DashboardImg from '../img/Dashboard.png';
import LibraryImg from '../img/Library.png';
import NotificationImg from '../img/Notification.png';
import UserImg from '../img/User.png';
import ToggleButton from '../img/toggleButton.png';
import LogoutImg from '../img/Icon ionic-ios-exit.svg';
import sword from '../img/sword.png';


class Toolbar extends Component {
    render() {

        return (
                    <div className="col-12 col-md-1 p-0 one">
                        <nav className="navbar navbar-expand flex-md-column flex-row align-items-start py-2 ">
                            <div className="collapse navbar-collapse ">
                                <ul className="flex-md-column flex-row navbar-nav w-100 justify-content-between">
                                    <li className="d-none d-md-inline nav-item">
                                        <a className="nav-link pl-0 text-nowrap" href="/DashboardLayout">
                                            <img className="" src={ToggleButton} alt="toggleButton" title="toggleButton" />
                                        </a>
                                    </li>
                                    <li className="d-md-none nav-item">
                                        <a className="nav-link pl-0 text-nowrap" href="#">
                                            <img className="" src={sword} alt="sword" title="sword" />
                                        </a>
                                    </li>
                                    <li className="nav-item ">
                                        <a className="nav-link pl-0 text-nowrap" href="/DashboardLayout">
                                            <img className="" src={DashboardImg} alt="Dashboard" title="Dashboard" />
                                            <br /><span className="d-none d-md-inline iconName">Dashboard</span>
                                        </a>
                                    </li>
                                    <li className="nav-item">
                                        <a className="nav-link pl-0 text-nowrap" href="/ServiceLibraryLayout">
                                            <img className="" src={LibraryImg} alt="LibraryImg" title="LibraryImg" />
                                            <br /><span className="d-none d-md-inline iconName">Service Library</span>
                                        </a>
                                    </li>
                                    <li className="nav-item">
                                        <a className="nav-link pl-0 text-nowrap" href="/OnboardingChatBotLayout">
                                            <img className="" src={NotificationImg} alt="NotificationImg" title="NotificationImg" />
                                            <br /><span className="d-none d-md-inline iconName">Notification</span>
                                        </a>
                                    </li>
                                    <li className="nav-item">
                                        <a className="nav-link pl-0 text-nowrap" href="/DashboardLayout">
                                            <img className="" src={UserImg} alt="UserImg" title="UserImg" />
                                            <br /><span className="d-none d-md-inline iconName">Admin</span>
                                        </a>
                                    </li>
                                    <li className ="nav-item">
                                        <a className="nav-link pl-0 text-nowrap" href="/LandingPage">
                                            <img className="" src={LogoutImg} alt="logout" title="logout" />
                                            <br /><span className="d-none d-md-inline iconName">Logout</span>
                                        </a>
                                    </li>
                                </ul>
                            </div>
                        </nav>
                       
                    </div>
              

        );
    }


}

export default Toolbar;