import React, { Component } from 'react';
import { Link } from "react-router-dom";
import "./OnboardingChatBot.css";
import { Input } from 'reactstrap';
import Arrow from '../img/whiteArrow.png'
import blackArrow from '../img/arrow.png'
import Doublecheck from '../img/Doublecheck.png';
import tick from '../img/tick.png';
import successImg from '../img/SuccessImg.png';
import loadingImg from '../img/loadingImg.png';



export default class OnBoardingService extends Component {
    state = {
        loading: false
    };
    fetchData = () => {
        this.setState({ loading: true })

        setTimeout(() => {
            this.setState({ loading: false });
        }, 10000)
        let element = document.getElementById("deployButton");
        element.classList.toggle("alternate-deployButton");

    };


    submitButton = () => {

        if (document.getElementById("acceptanceButton")) {
            document.getElementById("acceptanceButton").style.display = "none";
            document.getElementById("alternate-acceptanceButton").style.display = "block";
            document.getElementById("forProgress").style.display = "block";
            document.getElementById("loadingImg").style.display = "block";
            setTimeout(this.check, 5000);


        }
    }
    check=()=>{
      //  document.getElementById("successMsg").style.display = "block";
        let element = document.getElementById("deployButton");
        element.classList.toggle("alternate-deployButton");
        document.getElementById("loadingImg").style.display = "none";
        document.getElementById("successImg").style.display = "block";
        document.getElementById("forProgress").style.display = "none";
        document.getElementById("AfterforProgress").style.display = "block";


    }

    declineSubmitButton = () => {
        if (document.getElementById("alternate-acceptanceButton")) {
            document.getElementById("acceptanceButton").style.display = "block";
            document.getElementById("alternate-acceptanceButton").style.display = "none";
            document.getElementById("successMsg").style.display = "none";
            document.getElementById("forProgress").style.display = "none";
            document.getElementById("loadingImg").style.display = "none";
            document.getElementById("successImg").style.display = "none";
            let element = document.getElementById("deployButton");
            element.classList.toggle("alternate-deployButton");
              let element1 = document.getElementById("proceedButton");
             element1.classList.toggle("proceedButtonAfterAccept");

        }
    }

    deployButton = () => {
        let element = document.getElementById("deployButton");
        if (element.classList.toggle("alternate-deployButton")) {
            alert("Please Submit Path to proceed");
            element.classList.toggle("alternate-deployButton");
        }
        else {
            if (document.getElementById("loadingImg")) {
               // document.getElementById("loadingImg").style.display = "none";
              //  document.getElementById("successImg").style.display = "block";
                document.getElementById("successMsg").style.display = "block";
                let element = document.getElementById("deployButton");
                element.classList.toggle("alternate-deployButton");
                let element1 = document.getElementById("proceedButton");
                element1.classList.toggle("proceedButtonAfterAccept");
            }
        }

    }
    proceedtoConfigurationButton = () => {
        let element = document.getElementById("proceedButton");
        if (element.classList.toggle("proceedButtonAfterAccept")) {
            alert("Please Deploy Service to proceed");
            element.classList.toggle("proceedButtonAfterAccept");
        }
        else {
            window.location = "/ChatBotConfigurationLayout";
        }

    }
    selectInstance(){
        window.location = "/CreateInstanceLayout";
    }
    render() {
        return (
            <div className="container-fluid" >
                <div className="row justify-content-between">
                    <div className="col-4 d-none d-md-inline Onboarding-logoImg" />
                    
                    <div className="col title">
                        <span className="titleName">ONBOARDING</span><br />
                        <span className="titleDescription">Start configuration of selected services</span>
                    </div>
                </div>
                <br />

                <div className="row">
                    <div className="col-6 d-flex d-flex justify-content-between instance-containerServices" >
                        <div className="p-2">
                            <span className=" instance-services1" >ZEVA</span>
                            <span className=" instance-services1">ZA</span>
                            <span className=" instance-services1">RPA</span>
                        </div>
                        <div className="p-2">
                            <span className="instance-services1" >ZEVA</span>
                            { /*<span className="instance-services1" >ZA</span>*/}
                        </div >
                        <div className="p-2" >
                        { /*   <span className="instance-services1">ZEVA</span>
                            <span className="instance-services1">ZA</span>*/}
                        </div>
                        <div className="p-2" >
                          { /*  <span className="instance-services1">ZEVA</span>*/}
                        </div>
                    </div>
                </div>
                
                <div className="row">
                    <div className="col-6 d-flex justify-content-between instance-containerLine" >
                        <div className="p-2" id="instance-first-tick-mark" onClick={this.selectInstance} >
                            <img className="tick" src={tick} alt="tick" />
                        </div>
                        <div className="p-2" id="instance-next-tick-mark" >
                            <img className="tick" src={tick} alt="tick" />
                        </div>
                        <div className="p-2" id="instance-next-tick-mark" >
                            <img className="tick" src={tick} alt="tick" />
                        </div>
                        <div className="p-2" id="instance-next-tick-mark" >
                            <img className="tick" src={tick} alt="tick" />
                        </div>
                    </div>
                </div>
                <div className="row">
                    <div className="col-7 d-flex justify-content-between instance-containerLabel" >
                        <span className="p-2" style={{ color: "black" }}>Selection</span>
                        <span className="p-2" style={{ color: "#919191" }}>Onboarding</span>
                        <span className="p-2" style={{ color: "#919191" }}>Configuration</span>
                        <span className="p-2" style={{ color: "#919191" }}>Integration</span>
                    </div>
                </div>
                <br />


                <div className="row col-12 subTitle">
                    <span style={{ fontWeight: "lighter", marginRight: "0.5rem" }}>Environment</span>
                    <span style={{ fontWeight: "bold" }}>Development</span>
                </div>
                <span className="row col-6 titleDescription" >Enter path for creating Environment</span>
                <br />

                <div className="row col-12">
                    <Input id="pathSpace" type="text" placeholder="Example: 52.172.146.59" name="deployementPath" style={{ backgroundColor: "#F0EFFF", border: "none", height: "50px", marginBottom: "3rem" }} />
                </div>



                <div className="row" style={{ marginTop: "-2.5rem" }}>
                    <div className="col-8"></div>
                    <div className="col-3 submitButton" id="acceptanceButton">
                        <div className="row">
                            <button className="col-6 d-inline-flex p-2 btn btn-default insideAcceptanceButton" onClick={this.submitButton}>
                                <div className="onboard-submit">SUBMIT</div>
                            </button>
                            <button className="col-6 d-inline-flex p-2 btn btn-default insideDeclineButton">
                                <div className="onboard-restart">RESTART</div>
                            </button>
                        </div>
                    </div>
                    <div className="col-3 alternate-acceptanceButton" id="alternate-acceptanceButton" style={{ display: "none" }}>
                        <div className="row">
                            <button className="col-6 d-inline-flex p-2 btn btn-default alternate-insideAcceptanceButton">
                                <img src={Doublecheck} alt="arrow" style={{ textAlign: 'center', marginLeft: "0rem", marginTop: "0rem", display: "inline-block" }} />
                                <div className="d-none d-md-inline alternate-onboard-submit">SUBMITED</div>
                            </button>
                            <button className="col-6 d-inline-flex p-2 btn btn-default  alternate-insideDeclineButton" onClick={this.declineSubmitButton}>
                                <div className="alternate-onboard-restart" >RESTART</div>
                            </button>
                        </div>
                    </div>
                </div>
                <br />


                <br />

                <div className="row">
                    <div className="col-1 "></div>
                    <div className="col-4" id="loadingImg" style={{ display: "none" }} >
                        <img className="loadingImg" src={loadingImg} alt="loadingImg" />
                    </div>
                    <div className="col-4" id="successImg" style={{ display: "none" }}>
                        <   img className="successImg" src={successImg} alt="successImg" />
                    </div>
                    <div className="col-4 forProgress" id="forProgress" style={{ display: "none" }}>
                        <div className="row col progress">
                            <div className="progress-bar progress-bar-striped progress-bar-animated bg-infoCheck" role="progressbar" style={{ width: "25%" }} aria-valuenow="25" aria-valuemin="0" aria-valuemax="100">25%</div>
                        </div>
                        <div className="row col onboardServicelabel">
                            <span>MYSql</span>
                        </div>
                        <div className="row col progress">
                            <div className="progress-bar progress-bar-striped progress-bar-animated bg-infoCheck" role="progressbar" style={{ width: "50%" }} aria-valuenow="50" aria-valuemin="0" aria-valuemax="100">50%</div>
                        </div>
                        <div className="row col onboardServicelabel">
                            <span>Mongo DB</span>
                        </div>
                        <div className="row col progress">
                            <div className="progress-bar progress-bar-striped progress-bar-animated bg-infoCheck" role="progressbar" style={{ width: "75%" }} aria-valuenow="75" aria-valuemin="0" aria-valuemax="100">75%</div>
                        </div>
                        <div className="row col onboardServicelabel">
                            <span>Tomcat</span>
                        </div>
                        <div className="row col progress">
                            <div className="progress-bar progress-bar-striped progress-bar-animated bg-infoCheck" role="progressbar" style={{ width: "100%" }} aria-valuenow="100" aria-valuemin="0" aria-valuemax="100">100%</div>
                        </div>
                        <div className="row col onboardServicelabel">
                            <span>Python</span>
                        </div>
                        <br />
                        <br />
                    </div>

                    <div className="col-4 forProgress" id="AfterforProgress" style={{ display: "none" }}>
                        <div className="row col progress">
                            <div className="progress-bar  bg-infoCheck" role="progressbar" style={{ width: "100%" }} aria-valuenow="100" aria-valuemin="0" aria-valuemax="100">100%</div>
                        </div>
                        <div className="row col onboardServicelabel">
                            <span>MYSql</span>
                        </div>
                        <div className="row col progress">
                            <div className="progress-bar  bg-infoCheck" role="progressbar" style={{ width: "100%" }} aria-valuenow="100" aria-valuemin="0" aria-valuemax="100">100%</div>
                        </div>
                        <div className="row col onboardServicelabel">
                            <span>Mongo DB</span>
                        </div>
                        <div className="row col progress">
                            <div className="progress-bar bg-infoCheck" role="progressbar" style={{ width: "100%" }} aria-valuenow="100" aria-valuemin="0" aria-valuemax="100">100%</div>
                        </div>
                        <div className="row col onboardServicelabel">
                            <span>Tomcat</span>
                        </div>
                        <div className="row col progress">
                            <div className="progress-bar  bg-infoCheck" role="progressbar" style={{ width: "100%" }} aria-valuenow="100" aria-valuemin="0" aria-valuemax="100">100%</div>
                        </div>
                        <div className="row col onboardServicelabel">
                            <span>Python</span>
                        </div>
                        <br />
                        <br />
                    </div>
                </div>
                <br/>
               
                <div className="row ">
                    <div className="col-8 "></div>
                    {/*<div className="col-2 ">
                        </div>*/}
                    <button className="col-3 btn btn-default deployButton" id="deployButton" onClick={this.deployButton}>
                        <label className="deployLabel">DEPLOY ZEVA</label>
                    </button>
                </div>



                <br />
                 <span className="row col-6 successMsgLabel" id="successMsg" style={{display:"none", marginLeft:"30rem"}} >ZEVA Deployed Successfully</span>
                <br />

                <div className="row " >
                    <div className="col-8 "></div>
                    {/*<div className="col-2 ">
                        </div>*/}

                    <button className="col-3 btn btn-default install-proceedButton" id="proceedButton" onClick={this.proceedtoConfigurationButton}>
                        <label className="proceedLabel">PROCEED TO ZEVA CONFIGURATION</label>

                    </button>

                </div>
                <br />
                <br />

            </div>
        )
    }

}
