import React, { Component }  from 'react';
import Logo from '../img/Logo.svg';
import {Link} from "react-router-dom";


class ZenAnalyticaDashboardLayout extends Component{
    
    render(){
        return(
            <div className="container-fluid">
                 <div className="row justify-content-between">
                    <div className="col-4 d-none d-md-inline Onboarding-logoImg" />
                    
                    <div className="col title">
                        <span className="titleName">ZenAnalytica ITSM DASHBOARD</span><br />
                        <span className="titleDescription"></span>
                    </div>
                </div>

                <div className="row col-12 video"
                    style={{
                        position: "relative",
                        paddingBottom: "56.25%" /* 16:9 */,
                        paddingTop: 50,
                        height: 0
                    }}>
                    <iframe
                        style={{
                        position: "absolute",
                        top: 40,
                        left: 0,
                        width: "100%",
                        height: "85%"
                        }}
                        src={`https://demo.zenanalytica.com/`}
                        title="zenAnalytica"
                        frameBorder="0"/>
                 </div>
                
            </div>
           
        )
    }

}
export default ZenAnalyticaDashboardLayout;