import React, { Component } from 'react';
import "./CreateInstance.css";
import tick from '../img/tick.png';
import plus from '../img/plus.png';
import edit from '../img/edit.png';
import ZEVA from '../img/ZEVA-1.png';
import ZenAnalytica_Logo from '../img/ZenAnalytica_Logo-1.png';
import Arrow from '../img/whiteArrow.png';
import RPA from '../img/RPA.png';
import $ from 'jquery';


class CreateInstance extends Component {

    zevaOnboarding = () => {
        window.location = "/InstallationGuideLayout";
    }
    rpaIntegration=()=>{
        window.location = "/RPAIntegrationLayout";
    }
    zenAnalyticaOnboarding=()=>{
        window.location = "/RecommConfigurationLayout";
    }
    zenAnalyticaOnboarding
    editButton=()=>{
        $(document).ready(function() {
            $('a.edit').click(function () {
                var dad = $(this).parent().parent();
                dad.find('label').hide();
                dad.find('input[type="text"]').show().focus();
            });
            
            $('input[type=text]').focusout(function() {
                var dad = $(this).parent();
                $(this).hide();
                dad.find('label').show();
            });
        });
    }

    selectInstance=()=>{
        window.location = "/CreateInstanceLayout";
    }

    render() {



        return (

            <div className="container-fluid" >
                <div className="row justify-content-between">
                    <div className="col instance-logoImg" />
                    <div className="col instance-title">
                        <span className="instance-titleName">SERVICE INSTANCE</span><br />
                        <span className="instance-titleDescription">Edit and configure instance</span>
                    </div>
                </div>
                <br />
                <div className="row">
                    <div className="col-6 d-flex justify-content-between instance-containerServices" >
                        <div className="p-2" >
                            <span className="instance-services1" >ZEVA</span>
                            <span className="instance-services1">ZA</span>
                            <span className="instance-services1">RPA</span>
                        </div>
                      

                    </div>
                </div>
                <div className="row ">
                    <div className="col-6 d-flex justify-content-between instance-containerLine" >
                        <div className="p-2" id="instance-first-tick-mark" onClick={this.selectInstance} >
                            <img className="tick" src={tick} alt="tick" />
                        </div>
                        <div className="p-2" id="instance-next-tick-mark" >
                            <img className="tick" src={tick} alt="tick" />
                        </div>
                        <div className="p-2" id="instance-next-tick-mark" >
                            <img className="tick" src={tick} alt="tick" />
                        </div>
                        <div className="p-2" id="instance-next-tick-mark" >
                            <img className="tick" src={tick} alt="tick" />
                        </div>
                       
                    </div>
                </div>
                <div className="row">
                    <div className="col-7 d-flex justify-content-between instance-containerLabel" >
                        <span className="p-2" style={{ color: "black" }}>Selection</span>
                        <span className="p-2" style={{ color: "#919191" }}>Onboarding</span>
                        <span className="p-2" style={{ color: "#919191" }}>Configuration</span>
                        <span className="p-2" style={{ color: "#919191" }}>Integration</span>
                      
                    </div>
                </div>
                <br />
                <br />
                <div className="row control-group">
                        <div className="col-4 instanceLabel">
                            <label htmlFor="name" className="control-label">
                                <p className=" instanceName">Give Instance Name<i className="icon-star"></i></p>
                                <span style={{ color: "black", fontFamily: "Helvetica", fontSize: "14px", fontWeight: "lighter", fontStyle: "oblique" }}>Service:</span>
                            </label>
                            <input type="text" className="edit-input" style={{display:"none"}}/>
                        </div>
                        <div className="col-3 controls createInstanceLabel" onClick={this.editButton}>
                            <a className="edit" href="#"> <img className="edit" src={edit} alt="edit" />
                            <span className="edit" style={{ color: "black", fontFamily: "Helvetica", fontSize: "12px", fontWeight: "lighter", fontStyle: "oblique" }}>Edit Name</span><br />
                            </a>
                        </div>
                </div>
                <br />
                <br />
                <div className="row">
                    <div className="card small-box">
                        <img className="Zeva" src={ZEVA} alt="ZEVA" />
                        <button className=" btn btn-default instance-inside-small-box" onClick={this.zevaOnboarding}>
                            <span className="inside-button"> Onboarding </span>
                            <img className="Arrow" src={Arrow} alt="arrow" />
                        </button>
                    </div>
                    <div className="small-box">
                        <img className="ZenAnalytica_Logo" src={ZenAnalytica_Logo} alt="ZenAnalytica_Logo" />
                        <button className=" btn btn-default instance-inside-small-box" onClick={this.zenAnalyticaOnboarding}>
                            <span className="inside-button"> Onboarding </span>
                            <img className="Arrow" src={Arrow} alt="arrow" />
                        </button>
                    </div>
                    <div className="small-box">
                        <img className="RPA" src={RPA} alt="RPA" />
                        <button className=" btn btn-default instance-inside-small-box" onClick={this.rpaIntegration}>
                            <span className="instance-inside-button"> RPA TO SAMURAI INTEGRATION </span>
                            <img className="Arrow" src={Arrow} alt="arrow" />
                        </button>
                    </div>
                    <div className="small-box">
                        <img className="plus" src={plus} alt="plus" />
                        <button className=" btn btn-default instance-inside-small-box" onClick={this.newOnboarding}>
                            <span className="inside-button"> Add Service </span>

                        </button>
                    </div>
                </div>


                <br />
                <br />

            </div>
        );
    }

}
export default CreateInstance;