import React, { Component } from 'react';
import Logo from '../img/Logo.svg';
import { Link } from "react-router-dom";
import tick from '../img/tick.png';
import plus from '../img/plus.png';
import edit from '../img/edit.png';
import ZEVA from '../img/ZEVA-1.png';
import ZenAnalytica_Logo from '../img/ZenAnalytica_Logo-1.png';
import RoboChamp from '../img/RoboChamp.png';
import ServiceNow from '../img/ServiceNow.png';
import Cherwell from '../img/Cherwell.png';
import bmc from '../img/bmc.png';
import hp from '../img/hp.png';
import PRTG from '../img/PRTG.png';
import Dynatrace from '../img/Dynatrace.png';
import splunk from '../img/splunk.png';
import Quest from '../img/Quest.png';
import NewRelic from '../img/NewRelic.png';
import AppDynamics from '../img/AppDynamics.png';
import ControlM from '../img/ControlM.png';
import blackArrow from '../img/arrow.png';
import Arrow from '../img/whiteArrow.png'
import sword from '../img/bluesword.png';
import './IntegrationOfServices.css';
import $ from 'jquery';


class IntegrationOfServices extends Component {


    proceedtoDashboardButton = () => {

        window.location = "/DashboardLayout";


    }

    retryIntegrationButton = () => {
        this.onArrowClick();
    }
    selectInstance(){
        window.location = "/CreateInstanceLayout";
    }
    selectOnBoarding(){
        window.location = "/OnboardingChatBotLayout";
    }

    selectConfiguration(){
        window.location = "/ChatBotConfigurationLayout";
    }
    render() {
        return (

            <div className="container-fluid" >
                <div className="row justify-content-between">
                    <div className="col-4 d-none d-md-inline Onboarding-logoImg" />
                    <div className="col title">
                        <span className="titleName">INTEGRATION</span><br />
                        <span className="titleDescription">Start Integrating selected services</span>
                    </div>
                </div>


                <div className="row">
                    <div className="col-6 d-flex d-flex justify-content-between instance-containerServices" >
                        <div className="p-2">
                            <span className=" instance-services1" >ZEVA</span>
                            <span className=" instance-services1">ZA</span>
                            <span className=" instance-services1">RPA</span>
                        </div>
                        <div className="p-2">
                            <span className="instance-services1" >ZEVA</span>
                            { /*<span className="instance-services1" >ZA</span>*/}
                        </div >
                        <div className="p-2" >
                           <span className="instance-services1">ZEVA</span>
                           { /* <span className="instance-services1">ZA</span>*/}
                        </div>
                        <div className="p-2" >
                           <span className="instance-services1">ZEVA</span>
                        </div>
                    </div>
                </div>
                
                <div className="row">
                    <div className="col-6 d-flex justify-content-between instance-containerLine" >
                        <div className="p-2" id="instance-first-tick-mark"  onClick={this.selectInstance}>
                            <img className="tick" src={tick} alt="tick" />
                        </div>
                        <div className="p-2" id="instance-first-tick-mark" onClick={this.selectOnBoarding} >
                            <img className="tick" src={tick} alt="tick" />
                        </div>
                        <div className="p-2" id="instance-first-tick-mark" onClick={this.selectConfiguration}>
                            <img className="tick" src={tick} alt="tick" />
                        </div>
                        <div className="p-2" id="instance-next-tick-mark" >
                            <img className="tick" src={tick} alt="tick" />
                        </div>
                    </div>
                </div>
                <div className="row">
                    <div className="col-7 d-flex justify-content-between instance-containerLabel" >
                        <span className="p-2" style={{ color: "black" }}>Selection</span>
                        <span className="p-2" style={{ color: "black" }}>Onboarding</span>
                        <span className="p-2" style={{ color: "black" }}>Configuration</span>
                        <span className="p-2" style={{ color: "#919191" }}>Integration</span>
                    </div>
                </div>
                <br />



                <div className="row col-12 subTitle">
                    <span style={{ fontWeight: "lighter", marginRight: "0.5rem" }}>ZEVA</span>
                    <span style={{ fontWeight: "bold" }}>Integration</span>
                </div>
                <br />

                <div className="row col-8 pipeline"></div>

                <div className="row col-6">
                    <span className="successMsgLabel">Integration Successful
                                </span>
                </div>
                <br />
                <br />
                <div className="row justify-content-center">
                    <div className=" card small-box">
                        <img className="integrate-Zeva" src={ZEVA} alt="ZEVA" />
                    </div>
                    <div className=" annimated-box">
                        <div className="gear" id="gear1"></div>
                        <div className="gear" id="gear2"></div>
                        <div className="gear" id="gear3"></div>
                       
                  
                    </div>
                    <div className=" card small-box" style={{marginLeft:"3.5rem"}}>
                        <img className="sword" src={sword} alt="sword" />
                    </div>

                </div>


                <div className="row" >
                    <div className="col-8 "></div>
                    {/*<div className="col-3 ">
                            </div>*/}
                    <button className="col-3 btn btn-default retryIntegrationBtn" id="retryIntegrationButton" onClick={this.retryIntegrationButton}>
                        <label className="retryIntegrationlbl">Retry Integration </label>
                    </button>
                </div>
                <br/>
                <div className="row">
                    <div className="col-8 "></div>
                    {/*<div className="col-3 ">
                        </div>*/}
                    <button className="col-3 btn btn-default config-proceedButton" id="config-proceedButton" onClick={this.proceedtoDashboardButton}>
                        <label className="config-proceedLabel">VIEW INSTANCE DASHBOARD </label>
                    </button>
                    
                </div>
                <br/>
                <br/>
            </div>

        )
    }

}
export default IntegrationOfServices;