import React, { Component } from 'react';
import bluesword from '../img/bluesword.png';
import ZEVA from '../img/ZEVA-1.png';
import ZenAnalytica_Logo from '../img/ZenAnalytica_Logo-1.png';
import RPA from '../img/RPA.png';
import RoboChamp from '../img/RoboChamp.png';
import ServiceNow from '../img/ServiceNow.png';
import Cherwell from '../img/Cherwell.png';
import bmc from '../img/bmc.png';
import hp from '../img/hp.png';
import PRTG from '../img/PRTG.png';
import Dynatrace from '../img/Dynatrace.png';
import splunk from '../img/splunk.png';
import Quest from '../img/Quest.png';
import NewRelic from '../img/NewRelic.png';
import AppDynamics from '../img/AppDynamics.png';
import ControlM from '../img/ControlM.png';
import './RunningInstance.css';
import tick from '../img/tick.png';


class RunningInstance extends Component {

    toggleButton = () => {
        if (document.getElementById("togBtn")) {
            let checkBox = document.getElementById("togBtn");
            let serviceBody = document.getElementById("serviceBody");
            if (checkBox.checked == true) {
                serviceBody.style.display = "block";
            } else {
                serviceBody.style.display = "none";
            }
        }
    }
    liveRunning = () => {
        window.location = "/LiveInstancesLayout";
    }
    thirdliveRunning=()=>{
        window.location = "/ZenAnalyticaDashboardLayout";
    }
    render() {
        return (

            <div className="container-fluid">
                <div className="row justify-content-around">
                    <div className="col-4 card running-service-outerbox">
                        <div className="row ">
                            <div className="col-12 running-header">
                                <span className="Running-headerName">ITSM Self-Help Support</span>
                            </div>
                        </div>
                        <div className="row justify-content-center" >
                            <div className="col-7">
                                <img className="RI-ZenAnalytica_Logo" src={ZenAnalytica_Logo} alt="ZenAnalytica_Logo" />
                            </div>
                        </div>
                        <div className="row justify-content-center" >
                            <div className="col-">
                                <img className="RI-Zeva" src={ZEVA} alt="ZEVA" />
                            </div>
                            <div className="col-4">
                                <img className="RPA" src={RPA} alt="RPA" />
                            </div>
                        </div>
                        <span className="row divider"></span>
                        <div className="row justify-content-center" >
                            <div className="col-2" onClick={this.liveRunning}>
                                <i className="fa fa-play-circle-o playButton" ></i>
                            </div>
                            <label className="col-4  switch">
                                <input type="checkbox" id="togBtn-2" onClick={this.secondtoggleButton} />
                                <div className="slider round">
                                    <span className="on">Active</span>
                                    <span className="off">Inactive</span>
                                </div>
                            </label>
                        </div>

                    </div>

                    <div className="col-4 card running-service-outerbox">
                        <div className="row ">
                            <div className="col-12 running-header">
                                <span className="Running-headerName">Virtual ITSM Assistance</span>
                            </div>
                        </div>
                        <div className="row justify-content-center" >
                            <div className="col-7">
                                <img className="RI-ZenAnalytica_Logo" src={ZenAnalytica_Logo} alt="ZenAnalytica_Logo" />
                            </div>
                        </div>
                        <div className="row justify-content-center" >
                            <div className="col-7">
                                <img className="RI-Zeva" src={ZEVA} alt="ZEVA" />
                            </div>
                        </div>
                        <span className="row divider"  style={{marginTop:"2rem", marginBottom:"rem"}}></span>
                        <div className="row justify-content-center" >
                            <div className="col-2" onClick={this.liveRunning}>
                                <i className="fa fa-play-circle-o playButton" ></i>
                            </div>
                            <label className="col-4  switch">
                                <input type="checkbox" id="togBtn-2" onClick={this.secondtoggleButton} />
                                <div className="slider round">
                                    <span className="on">Active</span>
                                    <span className="off">Inactive</span>
                                </div>
                            </label>
                        </div>

                    </div>

                </div>
                <br />
                <div className="row justify-content-around">
                    <div className="col-4 card running-service-outerbox">
                        <div className="row ">
                            <div className="col-12 running-header">
                                <span className="Running-headerName">Zen Analytica DashBoard</span>
                            </div>
                        </div>
                        <div className="row justify-content-center" >
                            <div className="col-7">
                                <img className="RI-ZenAnalytica_Logo" src={ZenAnalytica_Logo} alt="ZenAnalytica_Logo" />
                            </div>
                        </div>
                       
                        <span className="row divider" style={{marginTop:"4rem", marginBottom:"2rem"}}></span>
                        <div className="row justify-content-center" >
                            <div className="col-2" onClick={this.thirdliveRunning}>
                                <i className="fa fa-play-circle-o playButton" ></i>
                            </div>
                            <label className="col-4  switch">
                                <input type="checkbox" id="togBtn-2" onClick={this.toggleButton} />
                                <div className="slider round">
                                    <span className="on">Active</span>
                                    <span className="off">Inactive</span>
                                </div>
                            </label>
                        </div>

                    </div>
                </div>
                <br/>
                <br/>
            </div>




        )
    }

}
export default RunningInstance;