import React, { Component } from 'react';
import './NewInstances.css';
import { Link } from "react-router-dom";
import sword from '../../img/sword.png';
import whiteArrow from '../../img/whiteArrow.png'
import tick from '../../img/tick.png';

class NewInstances extends Component {

    selectInstance = () => {

        window.location = "/CreateInstanceLayout";
    }

    selectOnboarding = () => {

        window.location.href = "/OnboardingChatBotLayout";
    }
    selectConfiguration = () => {

        window.location.href = "/ChatBotConfigurationLayout";
    }
    selectIntegration = () => {

        window.location.href = "/IntegrationOfServiceLayout";
    }
    selectLive = () => {

        window.location.href = "/DashboardLayout";
    }
    
    render() {

        return (
            <div className="container-fluid" >
                <br />
                <div className="row col-12 whitebox">
                    <div className="row justify-content-between">
                        <div className='col-3 coloredBox' >
                            <div className="insideColoredBox" style={{ textAlign: 'center', fontFamily: 'Helvetica' }}>ITSM Self-Help Support</div>
                            <div className="more">
                                <Link to="/LibraryLayout" style={{ color: "white", fontSize: "18px" }}>more
                                        <img src={whiteArrow} alt="arrow" />
                                </Link>
                            </div>
                        </div>

                        <div className="col-9 ">
                            <div className="row">
                                <div className="col-8 d-flex d-flex justify-content-between newInstance-containerServices" >
                                    <div className="p-2">
                                        <span className=" newInstance-services1"><p >ZEVA</p>
                                            <p style={{ marginTop: "-1.5rem" }}>ZA</p>
                                            <p style={{ marginTop: "-1.5rem" }}>RPA</p></span>

                                    </div>
                                    <div className="p-2">
                                        <span className=" newInstance-services1"><p >ZEVA</p>
                                            <p style={{ marginTop: "-1.5rem" }}>ZA</p>
                                            <p style={{ marginTop: "-1.5rem" }}>RPA</p></span>

                                    </div >
                                    <div className="p-2" >
                                        <span className=" newInstance-services1"><p >ZEVA</p>
                                            <p style={{ marginTop: "-1.5rem" }}>ZA</p>
                                            <p style={{ marginTop: "-1.5rem" }}>RPA</p></span>

                                    </div>
                                    <div className="p-2" >
                                        <span className=" newInstance-services1"><p >ZEVA</p>
                                            <p style={{ marginTop: "-1.5rem" }}>ZA</p>
                                            <p style={{ marginTop: "-1.5rem" }}>RPA</p></span>

                                    </div>
                                    <div className="p-2" >
                                    <span className=" newInstance-services1"><p >ZEVA</p>
                                            <p style={{ marginTop: "-1.5rem" }}>ZA</p>
                                            <p style={{ marginTop: "-1.5rem" }}>RPA</p></span>

                                    </div>
                                </div>
                            </div>
                            <div className="row">
                                <div className="col-8 d-flex justify-content-between newInstance-containerLine" >
                                    <div className="p-2" id="newInstance-first-tick-mark" onClick={this.selectInstance} >
                                        <img className="tick" src={tick} alt="tick" />
                                    </div>
                                    <div className="p-2" id="newInstance-first-tick-mark" onClick={this.selectOnboarding}>
                                        <img className="tick" src={tick} alt="tick" />
                                    </div>
                                    <div className="p-2" id="newInstance-first-tick-mark" onClick={this.selectConfiguration}>
                                        <img className="tick" src={tick} alt="tick" />
                                    </div>
                                    <div className="p-2" id="newInstance-first-tick-mark" onClick={this.selectIntegration}>
                                        <img className="tick" src={tick} alt="tick" />
                                    </div>
                                    <div className="p-2" id="newInstance-first-tick-mark" onClick={this.selectLive}>
                                        <img className="tick" src={tick} alt="tick" />
                                    </div>
                                </div>
                            </div>
                            <div className="row">
                                <div className="col-8 d-flex justify-content-between newInstance-containerLabel" >
                                    <span className="p-2" style={{ color: "black" }}>Selection</span>
                                    <span className="p-2" style={{ color: "black" }}>Onboarding</span>
                                    <span className="p-2" style={{ color: "black" }}>Configuration</span>
                                    <span className="p-2" style={{ color: "black" }}>Integration</span>
                                    <span className="p-2" style={{ color: "black" }}>Live</span>
                                </div>
                            </div>
                        </div>
                        <div className="col-3"></div>
                    </div>

                </div>

                <br />
                <br />

                <br />

            </div>

        );
    }


}

export default NewInstances;