import React, { Component } from 'react';
import './Dashboard.css';
import NewInstances from './NewInstances/NewInstances';
import RunningInstance from '../RunningInstanceLayout/RunningInstance';


class Dashboard extends Component {

    leftTOrightChange=()=>{
      
        var mainFrameOne = document.getElementById("running"); 
        var mainFrameTwo = document.getElementById("new");
        var mainFrameThree = document.getElementById("newInstance"); 
        var mainFrameFour = document.getElementById("runningInstance");
     
        mainFrameOne.style.display = (
            mainFrameOne.style.display == "none" ? "block" : "none"); 
        mainFrameTwo.style.display = (
            mainFrameTwo.style.display == "none" ? "block" : "none"); 
        mainFrameThree.style.display = (
            mainFrameThree.style.display == "none" ? "block" : "none"); 
        mainFrameFour.style.display = (
            mainFrameFour.style.display == "none" ? "block" : "none"); 
    
}
    render() {

        return (
            <div className="container-fluid" >
                <div className="row justify-content-between">
                    <div className="col-4 d-none d-md-inline logoImg" />
                   
                    <div className="col title">
                        <span className="titleName">DASHBOARD</span><br />
                        <span className="titleDescription">Navigate through running and new instances</span>
                    </div>
                </div>
                <br />
        
                <div className="row col-12 rectangelBox">
                    <i className="fa fa-angle-double-left " style={{fontSize:"44px",color:"#ffffff" ,marginTop:"1rem"}} onClick={this.leftTOrightChange}></i>
                    <div className="running" id="running" style={{display:"block"}}>
                        <span className="runningLabel" id="runningLabel">Running </span>
                        <span className="runningInstanceLabel" id="runningInstanceLabel">Service Instances</span>
                    </div>
                    <div className="new" id="new" style={{display:"none"}}>
                        <span className="runningLabel" id="runningLabel">New </span>
                        <span className="runningInstanceLabel" id="runningInstanceLabel">Service Instances</span>
                    </div>
                    <i className="fa fa-angle-double-right " style={{fontSize:"44px",color:"#ffffff" ,marginTop:"1rem", marginLeft:"14rem"}} onClick={this.leftTOrightChange}></i>
                </div>
                
                <br />

                <div className="newInstance" id="newInstance" style={{ display: "none" }}><NewInstances /></div>
                <div className="runningInstance" id="runningInstance" style={{ display: "block" }}><RunningInstance /></div>
            </div>

        );
    }


}

export default Dashboard;