import React, { Component }  from 'react';
import "./Login.css"
import { Form, FormGroup, Label, Input} from 'reactstrap';
import {Redirect} from 'react-router-dom';
import personWithDoor from '../img/personWithDoor.png';



export default class Login extends Component{
    constructor(props){
        super(props)
        let loggedIn = false
        this.state = {
            email: '',
            password: '',
            loggedIn
        }

        this.onChange = this.onChange.bind(this)
        this.submitForm = this.submitForm.bind(this)
    }

onChange(e){
    this.setState({
        [e.target.name]: e.target.value
    })
}

submitForm(e){
    e.preventDefault()
    const {email, password} = this.state

    //logic
    if(email === "admin@zensar.com" && password === "admin")
    {
        localStorage.setItem("token", "123456789")
        this.setState({
            loggedIn: true
        })
    }

}
    render(){
        if(this.state.loggedIn){
            return <Redirect to = "/ServiceLibraryLayout"/>
        }
        return(
            <div>
                <div className="backgroundPage">
                    <div className="container">
                        <div className="row ">
                            <div className="col-9 insidebox" style={{marginLeft:"-8rem"}}>
                                <div className="row">
                                    <div className="col login-logoImg" />
                                    <div className="col login-title">
                                        <span className="login-titleName">LOGIN</span><br />
                                        <span className="titleDescription">Access our DASHBOARD after login</span>
                                    </div>
                                </div>
                              
                                <div className="row justify-content-between">
                                    <div className="col" style={{marginTop:"1.8rem"}}>
                                        <Form  onSubmit= {this.submitForm}>
                                            <div className="form-group">
                                                <input type="email" className="form-control emailBox" placeholder = "Enter Email" name="email"  value={this.state.email} onChange={this.onChange}/>
                                            </div>
                                            <div className="form-group">
                                                <input type="password" className="form-control passwordBox" placeholder = "Password" name="password" value={this.state.password} onChange={this.onChange}/>
                                            </div>
                                            <div className="form-check saveCheckBox">
                                                <input type="checkbox" className="form-check-input " id="exampleCheck1"/>
                                                <label className="form-check-label" htmlFor="exampleCheck1">Save Password</label>
                                            </div>
                                            <button type="Submit" className="login-submit"><span className="submitLabel">LOGIN</span></button>
                                        </Form>
                                    </div>
                                    <div className="col">
                                        <img className="personWithDoor" src={personWithDoor} alt="personWithDoor" />
                                    </div>
                                </div>
                            </div>
                      
                    </div>
                    </div>
                </div>
            </div>
        )
    }
}